.b-signup {
  padding: 16px;
  margin: auto;
  width: 340px;
  height: auto;
  background-color: #fff; }
  .b-signup > h2 {
    text-align: center; }
  .b-signup > p.success {
    color: var(--color-admin-primary); }
  .b-signup > p.error {
    color: var(--color-error); }
  .b-signup > form > * + * {
    margin-top: 20px; }
  .b-signup__switch {
    text-align: center; }
  .b-signup__box-button .btn {
    width: 100%; }
