.loader {
  position: relative;
  margin: 0 auto;
  width: 70px;
  height: 100%; }
  .loader:before {
    content: '';
    display: block;
    padding-top: 100%; }
  .loader__circular {
    animation: rotate 2s linear infinite;
    height: 100%;
    transform-origin: center center;
    width: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto; }
  .loader__path {
    stroke: var(--color-black-54);
    stroke-width: 4;
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
    animation: dash 1.5s ease-in-out infinite;
    stroke-linecap: square; }
  .loader_type-button .loader__path {
    stroke: #fff;
    stroke-width: 6; }
  .loader_type-cross-button {
    stroke: var(--color-black-54);
    stroke-width: 4;
    width: 30px;
    height: 70%; }

@keyframes rotate {
  100% {
    transform: rotate(360deg); } }

@keyframes dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0; }
  50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35px; }
  100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124px; } }

@keyframes color {
  100%,
  0% {
    stroke: #fff; }
  40% {
    stroke: #fff; }
  66% {
    stroke: #fff; }
  80%,
  90% {
    stroke: #fff; } }
