.b-signin {
  padding: 16px;
  margin: auto;
  width: 340px;
  height: auto;
  background-color: #fff; }
  .b-signin > h2 {
    text-align: center; }
  .b-signin > p.success {
    color: var(--color-admin-primary); }
  .b-signin > p.error {
    color: var(--color-error); }
  .b-signin > form > * + * {
    margin-top: 20px; }
  .b-signin__switch {
    text-align: center; }
  .b-signin__box-button .btn {
    width: 100%; }
